.checkbox-wrap {
    display: block;
    position: relative;
    padding-left: 30px;
    margin-bottom: 12px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 500;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}


/* Hide the browser's default checkbox */

.checkbox-wrap input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0;
}


/* Create a custom checkbox */

.checkmark {
    position: absolute;
    top: 0;
    left: 0;
}


/* Create the checkmark/indicator (hidden when not checked) */

.checkmark:after {
    content: "\f0c8";
    position: absolute;
    color: rgba(0, 0, 0, 0.1);
    font-size: 20px;
    margin-top: -4px;
    transition: 0.3s;
}

@media (prefers-reduced-motion: reduce) {
    .checkmark:after {
        transition: none;
    }
}


/* Show the checkmark when checked */

.checkbox-wrap input:checked~.checkmark:after {
    display: block;
    content: "\f14a";
    color: rgba(0, 0, 0, 0.2);
}


/* Style the checkmark/indicator */

.checkbox-primary {
    color: #f35588;
}

.checkbox-primary input:checked~.checkmark:after {
    color: #f35588;
}

.btn {
    cursor: pointer;
    box-shadow: none !important;
    font-size: 15px;
    padding: 10px 20px;
    border-radius: 10px;
    transition: all ease 0.2s;
}

.btn:hover,
.btn:active,
.btn:focus {
    outline: none;
}

.btn.btn-primary {
    background: #f35588;
    border: 1px solid #f35588;
    color: #fff;
    background: #f75959;
    background: -webkit-gradient(left top, right bottom, color-stop(0%, #f75959), color-stop(100%, #f35587));
    background: linear-gradient(135deg, #f75959 0%, #f35587 100%);
    filter: progid: DXImageTransform.Microsoft.gradient( startColorstr='#f75959', endColorstr='#f35587', GradientType=1);
}

.btn-primary-custom {
    background: #09333f;
    border: 1px solid #09333f;
    color: #fff;
    transition: .3s all ease;
}

.btn-primary-custom:hover {
    background: #104553;
    border: 1px solid #104553;
    color: #FFF;
}

.btn.btn-primary:hover {
    border: 1px solid #f35588;
    background: #f35588;
    color: #fff;
}

.btn.btn-primary.btn-outline-primary {
    border: 1px solid #f35588;
    background: transparent;
    color: #f35588;
}

.btn.btn-primary.btn-outline-primary:hover {
    border: 1px solid transparent;
    background: #f35588;
    color: #fff;
}

.btn.btn-green {
    background: #09333f;
    border: 1px solid #09333f;
    color: #fff;
    border-radius: 5px!important;
    transition: .3s all ease;
}

.btn.btn-green:hover {
    background: #104553;
    color: #fff;
}


.btn.btn-white {
    background: #fff;
    border: 1px solid #fff;
    color: #fff;
}

.btn.btn-white:hover {
    border: 1px solid #fff;
    background: transparent;
    color: #fff;
}

.btn.btn-white.btn-outline-white {
    border: 1px solid #fff;
    background: transparent;
    color: #fff;
}

.btn.btn-white.btn-outline-white:hover {
    border: 1px solid transparent;
    background: #fff;
    color: #000;
}


